const filterReducer = (state = '', action) => {
  switch (action.type) {
  case 'SET_FILTER':
    return action.filter
  case 'CLEAR_FILTER':
    return action.filter
  default:
    return state
  }
}

export const filterChange = filter => {
  return {
    type: 'SET_FILTER',
    filter,
  }
}

export const filterClear = () => {
  return {
    type: 'CLEAR_FILTER',
    filter: '',
  }
}

export default filterReducer